import React, { lazy, useEffect, useState } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Select from 'react-select'
import HashLoader from "react-spinners/HashLoader";
import '@fortawesome/fontawesome-free/css/all.min.css'
import {ReactComponent as Beras, ReactComponent} from './grain.svg'
import {ReactComponent as Garam} from './salt.svg'
import Garem from './salt.png'
import Beraz from './rice.png'
import Ikang from './fish.png'
import {ReactComponent as Ikan} from './fish.svg'
import {ReactComponent as Nodata} from './nodata.svg'
import Swal from 'sweetalert2'
import {
  CBadge,
  CSelect,
  CButton,
  CInput,
  CImg,
  CModal,
  CButtonGroup,
  CCard,
  CAlert,
  CLink,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout,
  CDataTable,
  CCollapse,
  CModalHeader,
  CModalBody,
  CFormGroup,
  CLabel
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {fieldviavalen} from './source.js'
import { AnimationWrapper } from 'react-hover-animation'
import store from '../../redux/store'
import swal from 'sweetalert';

const Noitems = () => {
    return(
        <div className="d-flex flex-column justify-content-center">
            <div className="d-flex justify-content-center pb-2">
                <Nodata/>
            </div>
            <div className="d-flex justify-content-center">
                <h5>
                    There is no data to display
                </h5>
            </div>
        </div>
    )
}

const ViaValenRKI = (props) => {
  const handleKomoditas = (e) =>{
    var value = e.target.id
    props.history.push('/RKI/form')
    store.dispatch({ type: 'komoditas/changeKomoditas', payload: value })
  }
  const [tambah, setTambah] = useState("")
  const [data, setData] = useState([
    {
      id:1,
      noaju:"4000077EEF8F61512202100001",
      komoditas:"Beras",
      tglaju:"",
      periode:"2021",
      status:"Draft"
    },
    {
      id:2,
      noaju:"4000088AAF5461512202000002",
      komoditas:"Garam",   
      tglaju:"05/03/2020",
      periode:"2020",
      status:"Proses di Kemenperin"
    }, 
    {
      id:3,
      noaju:"400007799F8F61513201900001",
      komoditas:"Garam",     
      tglaju:"21/03/2019",
      periode:"2019",
      status:"Penerbitan LHV"
    },     
    {
      id:4,
      noaju:"400007799G9G41514201900003",
      komoditas:"Beras",      
      tglaju:"10/03/2019",
      periode:"2019",
      status:"Penolakan Kemenperin"
    },       
    {
      id:5,
      noaju:"4000099OOF8K61512201800001",
      komoditas:"Beras",      
      tglaju:"15/03/2018",
      periode:"2018",
      status:"Penerbitan LHV"
    },      
  ])
  const [details,setDetails] = useState([])
  const toggleDetails = (index) =>{
    const posisi = details.indexOf(index)
    let newDetails = details.slice()
    if(posisi !== -1){
      newDetails.splice(posisi, 1)
    } else {
      newDetails = [...details, index]
    }
    setDetails(newDetails)
  }
  useEffect(() => {
    if(tambah == "ya"){
        props.history.push('/RKI/form')
    }
  }); 
  const [modalkomoditas, setModalkomoditas] = useState(false)
  // const swalCustom = Swal.mixin({
  //   customClass: {
  //     confirmButton: 'btn btn-lg mantasi',
  //     cancelButton: 'btn btn-lg btn-secondary'
  //   },
  //   buttonsStyling: false
  // }) 
  const [loading, setLoading] = useState(false)
  const handleSudah = () =>{
    setLoading(true)
  } 
  const swalfire = () =>{
    setLoading(false)
    Swal.fire({
      text: 'Apakah Anda Sudah Tahu Hendak Mengajukan Ijin Apa ?',
      icon: 'question',
      focusConfirm: false,
      showCancelButton: true,      
      confirmButtonText:
        '<i class="fas fa-check"></i> Sudah',
      cancelButtonText:
        '<i class="fas fa-times"></i> Belum',
    }).then((r)=>{
      if(r.isDismissed){
        
      }
      if(r.isConfirmed){
        props.history.push('/kemenperin')
      }
    })
  }
  const advanceSearch = () =>{
    setLoading(false)    
    setAdvance(!advance)
  }
  const handleTambah = (e) => {
    setLoading(true)
    if(e == "modal"){
      setTimeout(()=>swalfire(), 1500)
    } else if (e == "search"){
      setTimeout(()=>advanceSearch(), 1500)
    }
  }
  const [advance, setAdvance] = useState(false)
  const [dataijin, setdDataijin] = useState([])
  const [value, setValue] = useState({
      "urijin":"",
      "rki":[
          {
              "noaju":"4000077EEF8F61512202100001",
              "tglaju":"05/09/2020",
              "nolhv":"LHV-123-TEST",
              "tgllhv":"10/11/2020",
              "lokasipabrik":"Jakarta Timur",
              "komoditas":"Beras",
              "periode":"2020",
          },
          {
              "noaju":"400007799G9G41514201900003",
              "tglaju":"06/09/2020",
              "nolhv":"LHV-123-TEST",
              "tgllhv":"10/11/2020",
              "lokasipabrik":"Jakarta Timur",              
              "komoditas":"Garam",
              "periode":"2020",
          },
          {
              "noaju":"4000099OOF8K61512201800001",
              "tglaju":"07/09/2020",
              "nolhv":"LHV-123-TEST",
              "tgllhv":"10/11/2020",
              "lokasipabrik":"Jakarta Timur",              
              "komoditas":"Ikan",
              "periode":"2020",
          },                    
      ],
      "garam":[
          {
              "kl":"Kementerian Perdagangan",
              "urijin":"PI Garam Bahan Baku dan Bahan Penolong Industri",
              "komoditas":"garam"
          },
          {
              "kl":"Kementerian Perdagangan",
              "urijin":"PI Garam Selain Bahan Baku dan Bahan Penolong",
              "komoditas":"garam"
          }
      ],
      "ikan":[
          {
              "kl":"Kementerian Perdagangan",
              "urijin":"PI Hasil Perikanan Kebutuhan Bahan Baku Industri/Bahan",
              "komoditas":"ikan"
          },
          {
              "kl":"Kementerian Perdagangan",
              "urijin":"PI Hasil Perikanan Selain Kebutuhan Bahan Baku Industri/Bahan",
              "komoditas":"ikan"
          }
      ],
      "beras":[
          {
              "kl":"Kementerian Perdagangan",
              "urijin":"PI Beras - Hibah",
              "komoditas":"beras"
          },
          {
              "kl":"Kementerian Perdagangan",
              "urijin":"PI Beras - Keperluan Lain (Bahan Baku Industri)",
              "komoditas":"beras"
          },
          {
              "kl":"Kementerian Perdagangan",
              "urijin":"PI Beras - Keperluan Lain (BUMN)",
              "komoditas":"beras"
          },
          {
              "kl":"Kementerian Perdagangan",
              "urijin":"PI Beras - Keperluan Umum (BULOG)",
              "komoditas":"beras"
          },                                        
      ]
  })
  const handleChange= (e) =>{
      setValue({...value, ["urijin"]:e.target.value})
  }
  const searchIjin = () =>{
      setLoading(false)
      var keyword = value.urijin.toLowerCase()
      setdDataijin(value[keyword])
      if (value[keyword] == undefined){
        Swal.fire({
            text: 'Uraian Ijin Tidak Ditemukan',
            icon: 'error',
            focusConfirm: false,    
            confirmButtonText:
              'OK',
          })
      }
  }
  const handleSearch = () => {
      setLoading(true)
      setTimeout(()=>searchIjin(), 1500)
  }
  const [selected, setSelected] = useState({komoditas:""})
  const [show, setShow] = useState(false)
  const handleNext = (jos, jis) =>{
    setSelected({...selected, ["urijin"]:jos, ["komoditas"]:jis})
    setShow(true)
    console.log(selected)
  }
  const [komoditas, setKomoditas] = useState()
  const handleForm = () =>{
    store.dispatch({ type: 'komoditas/changeKomoditas', payload: selected.komoditas })
    store.dispatch({ type: 'izin/updateIzin', payload: true })
    store.dispatch({ type: 'izin/updatePI', payload:selected.urijin})
    props.history.push('/ssm-izin/form')      
  }
  return (
    <> 
    <CModal
    show={show} 
    size="xl"  
    onClose={()=>setShow(false)} 
    >
        <CModalHeader className="mantabjiwo" style={{backgroundColor:"#02265d", color:"white"}} closeButton>
            <h5>Pilih Hasil Verifikasi {selected.komoditas.charAt(0).toUpperCase() + selected.komoditas.slice(1)}</h5>
        </CModalHeader>
        <CModalBody>
            <CDataTable
            border
            addTableClasses="josss"
            items={value.rki.filter((x) => x.komoditas.toLowerCase() == selected.komoditas)}
            fields={[
                { key: 'no',  _style: { width: "1%"}, label:"No" },
                { key: 'subkomoditas', label:"Subkomoditas" },                                 
                { key: 'noaju', label:"Nomor Aju" }, 
                { key: 'tglaju', label:"Tanggal Aju" },                 
                { key: 'nolhv',  label:"Nomor LHV" }, 
                { key: 'tgllhv',  label:"Tanggal LHV" }, 
                { key: 'lokasipabrik',  label:"Lokasi Pabrik" },
                { key: 'periode',  label:"Periode" },
                { key: 'aksi',  label:"Aksi" }                                  
            ]}
            scopedSlots={{
                'subkomoditas':
                (item)=>{
                    return(
                        <td>
                            {selected.urijin.slice(2, selected.urijin.length)}
                        </td>                
                    )
                },
                'no':
                (item, index)=>{
                    index += 1
                    return(
                        <td>
                            {index}
                        </td>
                    )
                },
                'aksi':
                (item) =>{
                    return(
                        <td>
                            <CButton onClick={()=>handleForm()} className="btn btn-sm" style={{backgroundColor:"#02265d", color:"white"}}>
                                <i class="fas fa-arrow-alt-circle-right"></i>
                            </CButton>                            
                        </td>                        
                    )
                }
            }}
            />
        </CModalBody>
    </CModal>
    <CCard>
        <CCardBody>
            <div>
            <div style={{backgroundColor:loading ? "#ffffff9a" : "#ffffff00"}} className="d-flex justify-content-center infront p-4">
            <HashLoader color="#02265d" loading={loading} size={100}/>          
            </div>                   
            </div>              
            <CRow className="pl-4">
                <CFormGroup>
                    <CLabel>Masukkan Uraian Izin</CLabel>
                    <CRow>
                        <CCol md="10">
                            <CInput onChange={(e)=>handleChange(e)} />
                        </CCol>
                        <CCol md="2">
                            <CButton onClick={()=>handleSearch()} style={{backgroundColor:"#02265d", color:"white"}}><i class="fas fa-search fa-lg"></i></CButton>
                        </CCol>
                    </CRow>
                </CFormGroup>
            </CRow>
            <CRow className="px-3">
                <CDataTable
                noItemsViewSlot={<Noitems/>}
                addTableClasses="josss"
                border
                items={dataijin}
                fields={[
                    { key: 'no',  _style: { width: "1%"}, label:"No" },
                    { key: 'kl', label:"Kementerian / Lembaga" }, 
                    { key: 'urijin',  label:"Uraian Izin" }, 
                    { key: 'aksi',  label:"Aksi" },                       
                ]}
                scopedSlots={{
                    'no':
                    (item, index) => {
                        var jk = JSON.stringify(item)
                        index += 1
                        return(
                            <td>
                                {index}
                            </td>
                        )
                    },
                    'aksi':
                    (item) => {
                        var jos = item.urijin
                        return(
                            <td>
                                <CButton onClick={()=>handleNext(jos, item.komoditas)} className="btn btn-sm" style={{backgroundColor:"#02265d", color:"white"}}>
                                    <i class="fas fa-arrow-alt-circle-right"></i>
                                </CButton>
                            </td>
                        )
                    }
                }}
                />
            </CRow>
        </CCardBody>
    </CCard>
    </>
  )
}

export default ViaValenRKI
